import React, { useState, useMemo } from "react";
import { Form, Button, Input, Card } from "antd";
import { useDispatch, useSelector } from "react-redux";
import { SearchOutlined } from "@ant-design/icons";
import { v4 as uuidv4 } from "uuid";

import TaskItem from "./TaskItem";

import {
  addTaskAction,
  editTaskAction,
  deleteTaskAction,
} from "../../../redux/actions";

const ToDoListPage = () => {
  const [searchKeyword, setSearchKeyword] = useState("");

  const dispatch = useDispatch();
  const { taskList } = useSelector((state) => state.taskReducer);

  const filterTaskList = taskList.filter((task) => {
    return task.title.toLowerCase().includes(searchKeyword.toLowerCase());
  });

  const handleAddTask = (values) => {
    dispatch(addTaskAction({
      data: {
        id: uuidv4(),
        ...values,
      }
    }));
  };

  const handleEditTask = (id, values) => {
    dispatch(editTaskAction({ id, data: values }));
  };

  const handleDeleteTask = (id) => {
    dispatch(deleteTaskAction({ id }));
  };

  const renderTaskItem = useMemo(() => {
    return filterTaskList.map((taskItem, taskIndex) => {
      return (
        <TaskItem
          key={taskIndex}
          data={taskItem}
          id={taskItem.id}
          handleEditTask={handleEditTask}
          handleDeleteTask={handleDeleteTask}
        />
      );
    });
  }, [filterTaskList]);

  return (
    <div>
      <Card title="To Do List">
        <Form
          name="addTask"
          labelCol={{ span: 4 }}
          wrapperCol={{ span: 20 }}
          initialValues={{ username: "Tun" }}
          onFinish={(values) => handleAddTask(values)}
        >
          <Form.Item
            label="Tiu "
            name="title"
            validateFirst
            rules={[
              {
                required: true,
                whitespace: true,
                message: "Bn cha nhp tiu !",
              },
              {
                min: 6,
                max: 32,
                message: "Tiu  phi nm trong khong 6-32 k t",
              },
            ]}
          >
            <Input />
          </Form.Item>

          <Form.Item
            label="Ni dung"
            name="description"
            rules={[{ required: true, message: "Bn cha nhp ni dung" }]}
          >
            <Input />
          </Form.Item>

          <Button type="primary" htmlType="submit" block>
            Thm Task
          </Button>
        </Form>
      </Card>
      <Input
        onChange={(e) => setSearchKeyword(e.target.value)}
        placeholder="Tm kim"
        suffix={<SearchOutlined />}
        style={{ marginTop: 16 }}
      />
      {renderTaskItem}
    </div>
  );
};

export default ToDoListPage;
